import React, { useState } from 'react'
import { Dialog, DialogBackdrop, DialogPanel } from '@headlessui/react';
import { useActions } from '../../hooks/actions';

export function GameResultModal({ isResultModalOpen, resultTitle, totalScore }) {
    const [isOpenModal, setIsOpenModal] = useState(isResultModalOpen);
    const { setIsGameOver } = useActions();

    const handleCloseModal = () => {
        setIsOpenModal(false);
        setIsGameOver(true);
    }
    return (
        <Dialog open={isOpenModal} onClose={setIsOpenModal} className="relative z-10">
            <DialogBackdrop
                transition
                className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity data-[closed]:opacity-0 data-[enter]:duration-300 data-[leave]:duration-200 data-[enter]:ease-out data-[leave]:ease-in"
            />

            <div className="fixed inset-0 z-10 w-screen overflow-y-auto">
                <div className="flex min-h-full items-end justify-center p-4 text-center sm:items-center sm:p-0">
                    <DialogPanel
                        transition
                        className="relative transform overflow-hidden rounded-lg bg-white text-left shadow-xl transition-all data-[closed]:translate-y-4 data-[closed]:opacity-0 data-[enter]:duration-300 data-[leave]:duration-200 data-[enter]:ease-out data-[leave]:ease-in sm:my-8 sm:w-full sm:max-w-lg data-[closed]:sm:translate-y-0 data-[closed]:sm:scale-95"
                    >
                        <div className="bg-white px-4 pb-1 pt-5 sm:p-6 sm:pb-1">
                            <div className='text-center mb-4'>
                                <h1 className='font-bold text-xl'>{resultTitle}</h1>
                                <h2>Total score: <strong>{totalScore}</strong></h2>
                            </div>
                            <div className=" px-4 py-2 sm:flex sm:flex-row-reverse sm:px-6">
                                <button
                                    type="button"
                                    data-autofocus
                                    onClick={() => handleCloseModal()}
                                    className="mt-3 inline-flex w-full justify-center rounded-md bg-green-400 px-3 py-2 text-sm font-semibold text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 hover:bg-green-200 sm:mt-0 sm:w-auto"
                                >
                                    Return to scoreboard
                                </button>
                            </div>
                        </div>
                    </DialogPanel>
                </div>
            </div>
        </Dialog>
    )
}
